* {
  margin: 0;
}

.App {
  background-color: #fafafa;
  height: 100%;
}

.app__header {
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  object-fit: contain;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
  z-index: 1;
}

.app__headerImage {
  object-fit: contain;
  width: 100px;
  /* height: 100px; */
}

.app__signUp {
  display: flex;
  flex-direction: column;
}

.app__signUp > center {
  display: flex;
  flex-direction: column;
}

.app__posts {
  padding: 20px;
}
